<template>
  <div class="cake-detail">
    <!-- ÂïÜÂìÅÂõæÁâá -->
    <div class="cake-image">
      <img :src="product.image" alt="ËõãÁ≥ïÂõæÁâá" class="product-image" />
    </div>
    
    <!-- ÂïÜÂìÅ‰ø°ÊÅØ -->
    <div class="info-container">
      <!-- ÂêçÁß∞‰∏é‰ª∑Ê†º -->
      <div class="header-section">
        <div class="name-description">
          <h1 class="product-name">{{ product.name }}</h1>
          <p class="product-description">{{ product.description }}</p>
        </div>
        <div class="product-price">¬•{{ (currentSpec.price || 0).toFixed(2) }}</div>
      </div>

      <!-- ËßÑÊ†ºÈÄâÊã© -->
      <div class="spec-section">
        <h3 class="section-title">ÂïÜÂìÅËßÑÊ†º</h3>
        <div class="spec-grid">
          <div
            v-for="spec in product.specs"
            :key="spec.id"
            :class="['spec-item', { active: currentSpec.id === spec.id }]"
            @click="selectSpec(spec)"
          >
            {{ spec.number }}
          </div>
        </div>
      </div>

      <!-- ÂïÜÂìÅËØ¶ÊÉÖ -->
      <div class="detail-grid">
        <div class="detail-item" v-if="currentSpec.length && currentSpec.width">
          <span class="detail-icon">üìè</span>
          <span>Á∫¶{{ currentSpec.length }}√ó{{ currentSpec.width }}cm</span>
        </div>
        <div class="detail-item" v-if="currentSpec.weight">
          <span class="detail-icon">‚öñÔ∏è</span>
          <span>Á∫¶{{ (currentSpec.weight || 0) / 1000 }}kg</span>
        </div>
        <div class="detail-item" v-if="product.sweet">
          <span class="detail-icon">üç¨</span>
          <span>ÁîúÂ∫¶Ôºö
            <el-rate v-model="product.sweet" disabled :max="5" />
          </span>
        </div>
      </div>

      <!-- ÊåâÈíÆ -->
      <div class="action-buttons">
        <button class="cart-btn" @click="addProductToCart()">
          <span>Âä†ÂÖ•Ë¥≠Áâ©ËΩ¶</span>
          <span class="plus-icon">+</span>
        </button>
        <button class="buy-btn" @click="showBuyDialog = true">Á´ãÂç≥Ë¥≠‰π∞</button>
      </div>
    </div>

    <!-- Ë¥≠‰π∞Êï∞ÈáèÂºπÁ™ó -->
    <el-dialog
      v-model="showBuyDialog"
      title="Á°ÆËÆ§Ë¥≠‰π∞"
      width="400px"
      :before-close="handleDialogClose"
      class="buy-dialog"
    >
      <div class="quantity-selector">
        <div class="product-info">
          <img :src="product.image" alt="ÂïÜÂìÅÂõæÁâá" class="dialog-product-image" />
          <div class="dialog-product-details">
            <h4>{{ product.name }}</h4>
            <p class="spec-text">{{ currentSpec.number }}</p>
            <p class="price-text">¬•{{ (currentSpec.price || 0).toFixed(2) }}</p>
          </div>
        </div>
        
        <div class="quantity-control">
          <span class="quantity-label">Ë¥≠‰π∞Êï∞ÈáèÔºö</span>
          <div class="quantity-input-group">
            <button 
              class="quantity-btn" 
              @click="decreaseQuantity"
              :disabled="quantity <= 1"
            >-</button>
            <input 
              v-model.number="quantity" 
              type="number" 
              min="1" 
              max="99"
              class="quantity-input"
              @change="validateQuantity"
            >
            <button 
              class="quantity-btn" 
              @click="increaseQuantity"
              :disabled="quantity >= 99"
            >+</button>
          </div>
        </div>

        <div class="total-section">
          <span class="total-label">Â∞èËÆ°Ôºö</span>
          <span class="total-price">¬•{{ ((currentSpec.price || 0) * quantity).toFixed(2) }}</span>
        </div>
      </div>
      
      <template #footer>
        <el-button @click="showBuyDialog = false" class="cancel-btn">ÂèñÊ∂à</el-button>
        <el-button 
          type="primary" 
          @click="handleBuyNow" 
          :loading="buyLoading"
          class="confirm-btn"
        >
          Á°ÆËÆ§Ë¥≠‰π∞
        </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { ElMessage } from 'element-plus'
import { getCakeById, getTastes } from '@/api/cake'
import { useCartStore } from '@/store/cart'

const route = useRoute()
const router = useRouter()
const tastes = ref([])
const fetchTastes = async () => {
  try {
    const res = await getTastes()
    tastes.value = res.data
  } catch (error) {
    console.error('Ëé∑ÂèñÂè£Âë≥Â§±Ë¥•', error)
  }
}

const product = ref({
  id: null, name: '', tasteId: null, categoryId: null,
  image: '', sweet: 0, description: '', specs: []
})
const currentSpec = ref({})
const selectSpec = (spec) => { 
  currentSpec.value = spec
  // ÂàáÊç¢ËßÑÊ†ºÊó∂ÈáçÁΩÆÊï∞Èáè‰∏∫1
  quantity.value = 1
}
const loading = ref(false)
const fetchProductDetail = async (id) => {
  try {
    loading.value = true
    const res = await getCakeById(id)
    if (!res.data) throw new Error('Êú™Ëé∑ÂèñÂà∞ËØ¶ÊÉÖ')
    product.value = { ...res.data, specs: res.data.specs || [] }
    if (product.value.specs.length > 0) {
      currentSpec.value = product.value.specs[0]
      // ÂàùÂßãÂåñÊï∞Èáè
      quantity.value = 1
    }
  } catch (err) {
    ElMessage.error('Âä†ËΩΩËõãÁ≥ï‰ø°ÊÅØÂ§±Ë¥•')
  } finally {
    loading.value = false
  }
}

// Ë¥≠‰π∞Áõ∏ÂÖ≥Áä∂ÊÄÅ
const showBuyDialog = ref(false)
const quantity = ref(1)
const buyLoading = ref(false)

// Êï∞ÈáèÊéßÂà∂ÊñπÊ≥ï
const increaseQuantity = () => {
  if (quantity.value < 99) {
    quantity.value++
  }
}

const decreaseQuantity = () => {
  if (quantity.value > 1) {
    quantity.value--
  }
}

const validateQuantity = () => {
  if (quantity.value < 1) quantity.value = 1
  if (quantity.value > 99) quantity.value = 99
}

const handleDialogClose = (done) => {
  done()
}

// Á´ãÂç≥Ë¥≠‰π∞
const handleBuyNow = () => {
  if (!currentSpec.value.id) {
    ElMessage.warning('ËØ∑ÂÖàÈÄâÊã©ÂïÜÂìÅËßÑÊ†º')
    return
  }

  buyLoading.value = true
  
  // ÊûÑÂª∫Ë¥≠‰π∞ÂïÜÂìÅÊï∞ÊçÆ
  const buyItem = {
    name: product.value.name,
    image: product.value.image,
    productId: product.value.id,
    num: quantity.value,
    number: currentSpec.value.number,
    amount: currentSpec.value.price,
    specId: currentSpec.value.id
  }

  // Ë∑≥ËΩ¨Âà∞Á°ÆËÆ§ËÆ¢ÂçïÈ°µÈù¢Ôºå‰º†ÈÄíÂïÜÂìÅÊï∞ÊçÆ
  try {
    const items = [buyItem]
    const queryString = encodeURIComponent(JSON.stringify(items))
    router.push({
      path: '/checkout',
      query: { items: queryString }
    })
  } catch (error) {
    ElMessage.error('Ë∑≥ËΩ¨Â§±Ë¥•ÔºåËØ∑ÈáçËØï')
  } finally {
    buyLoading.value = false
    showBuyDialog.value = false
  }
}

const cartStore = useCartStore()
const addProductToCart = () => {
  try {
    cartStore.addItem({
      name: product.value.name,
      image: product.value.image,
      productId: product.value.id,
      num: 1,
      number: currentSpec.value.number,
      amount: currentSpec.value.price,
      specId: currentSpec.value.id
    })
    ElMessage.success('Ê∑ªÂä†Ë¥≠Áâ©ËΩ¶ÊàêÂäü')
  } catch (error) {
    ElMessage.error('Ê∑ªÂä†Ë¥≠Áâ©ËΩ¶Â§±Ë¥•')
  }
}

onMounted(async () => {
  await fetchTastes()
  if (route.params.id) fetchProductDetail(route.params.id)
})
</script>

<style scoped>
